import * as React from 'react'

import CodeBox from '../components/codebox/CodeBox'
import Button from './components/button/Button'
import Popover from './components/popover/Popover'


const content = (
    <div style={{ width: "150px", padding: "5px 20px" }}>
        <div style={{
            fontSize: "16px",
            lineHeight: " 24px",
            color: " rgba(0, 0, 0, 0.65)",
            boxShadow: " 0 4px 4px -4px rgb(0 0 0 / 10%)",
            padding: " 5px 20px",
        }}>Title</div>
        <ul className="content-list"><li style={{
            fontSize: "14px",
            lineHeight: "22px",
            color: " rgba(0, 0, 0, 0.45)"
        }}>Content</li><li style={{
            fontSize: "14px",
            lineHeight: "22px",
            color: " rgba(0, 0, 0, 0.45)"
        }}>Content</li><li style={{
            fontSize: "14px",
            lineHeight: "22px",
            color: " rgba(0, 0, 0, 0.45)"
        }}>Content</li></ul>
    </div>
)
const componentName = 'ButtonPage'
export default () => {
    return (
        <div>
            <section>
                <h1>Popover 气泡卡片</h1>
                <p className="text">触发时弹出卡片浮层。</p>
            </section>
            <section>
                <h2>何时使用</h2>
                <p className="text">根据用户的操作行为展现目标元素的进一步提示或相关操作时。</p>
            </section>
            <section>
                <h2>代码示例</h2>
            </section>
            <div className="example">
                <div className="container">
                    <Popover trigger="hover" content={content} position="top"><Button type="primary">hover</Button></Popover>
                    <span>{" "}</span>
                    <Popover trigger="click" content={content} position="top"><Button type="primary">click</Button></Popover>
                </div>

                <CodeBox
                    title="触发方式"
                    description="支持 hover, click触发方式。"
                >

                    {'折叠代码区'}
                </CodeBox>
            </div>

            <section>
                <h2>API</h2>
            </section>
            <table className="api-table">
                <thead>
                    <tr>
                        <th>参数</th>
                        <th className="des">说明</th>
                        <th className="type">类型</th>
                        <th>默认值</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <td>content</td>
                        <td className="des">浮层内容</td>
                        <td className="type">string | React.ReactNode</td>
                        <td>——</td>
                    </tr>
                    <tr>
                        <td>trigger</td>
                        <td className="des">触发方式</td>
                        <td className="type">'hover' | 'click'</td>
                        <td>'hover'</td>
                    </tr>
                </tbody>
            </table>
            {/* <section>
        <h2>Method</h2>
      </section>
      <table className="method-table">
        <thead>
          <tr>
            <th>名称</th>
            <th className="des">描述</th>
            <th>参数</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>focus()</td>
            <td className="des">获取焦点</td>
            <td className="params">——</td>
          </tr>
          <tr>
            <td>blur()</td>
            <td className="des">移除焦点</td>
            <td className="params">——</td>
          </tr>
        </tbody>
      </table> */}
        </div>
    )
}



